import React, { useState } from 'react';
import styled from 'styled-components';
import { NavLink } from 'react-router-dom';

const SidebarContainer = styled.nav`
  position: fixed;
  left: 0;
  top: 0;
  width: ${props => (props.open ? '220px' : '72px')};
  height: 100vh;
  background: #172a3a;
  color: #fff;
  transition: width 0.18s cubic-bezier(.4,0,.2,1);
  box-shadow: 2px 0 12px rgba(0,0,0,0.04);
  display: flex;
  flex-direction: column;
  z-index: 100;
`;

const ToggleButton = styled.button`
  background: none;
  border: none;
  color: #fff;
  margin: 18px 0 18px 18px;
  font-size: 1.7rem;
  cursor: pointer;
  align-self: flex-start;
  outline: none;
`;

const SidebarContent = styled.div`
  margin-top: 24px;
  display: flex;
  flex-direction: column;
  gap: 36px;
  padding: 0 18px;
`;

const Menu = styled.ul`
  list-style: none;
  padding: 0;
  margin: 0;
  display: flex;
  flex-direction: column;
  gap: 10px;
`;

const SidebarTitle = styled.h3`
  font-size: 1.15rem;
  font-weight: 700;
  color: #fff;
  margin-bottom: 6px;
  letter-spacing: 0.5px;
`;

const MenuItem = styled(NavLink)`
  display: block;
  font-size: 1.08rem;
  padding: 8px 0 8px 10px;
  border-radius: 6px;
  color: #cfd8dc;
  text-decoration: none;
  transition: background 0.13s, color 0.13s;
  margin-right: 8px;
  &.active, &:hover {
    background: #22344a;
    color: #fff;
  }
`;

const InfoBox = styled.div`
  background: #1e3247;
  border-radius: 7px;
  padding: 13px 13px 10px 13px;
  font-size: 0.98rem;
  color: #b0bec5;
  margin-top: 18px;
  box-shadow: 0 1px 3px 0 rgba(0,0,0,0.04);
  strong {
    color: #fff;
    font-size: 1.02rem;
    display: block;
    margin-bottom: 4px;
  }
`;

export default function Sidebar() {
  const [open, setOpen] = useState(true);

  return (
    <SidebarContainer open={open}>
      <ToggleButton onClick={() => setOpen(o => !o)} title={open ? 'Recolher' : 'Expandir'}>
        {open ? '⟨' : '⟩'}
      </ToggleButton>
      {open && (
        <SidebarContent>
          <div>
            <SidebarTitle>Admin</SidebarTitle>
            <Menu>
              <li><MenuItem to="/dashboard">Dashboard</MenuItem></li>
              <li><MenuItem to="/pedidos">Pedidos</MenuItem></li>
              <li><MenuItem to="/usuarios">Usuários</MenuItem></li>
              <li><MenuItem to="/configuracoes">Configurações</MenuItem></li>
              <li><NavLink to="/catalogo-itens" className={({ isActive }) => isActive ? 'active' : ''}>
                <span role="img" aria-label="Itens">📦</span> Catálogo de Itens
              </NavLink></li>
            </Menu>
          </div>
          <InfoBox>
            <strong>Informações</strong>
            Status: <span style={{color: '#4caf50'}}>Online</span><br/>
            Pedidos pendentes: <span style={{color: '#fff'}}>5</span>
          </InfoBox>
        </SidebarContent>
      )}
    </SidebarContainer>
  );
}
