import React from 'react';
import { ThemeProvider } from 'styled-components';
import { theme } from './theme';
import { GlobalStyle } from './globalStyles';
import { BrowserRouter as Router, Routes, Route, Navigate } from 'react-router-dom';
import LoginPage from './pages/LoginPage';
import AdminDashboard from './pages/AdminDashboard';
import ProtectedRoute from './components/ProtectedRoute';
import NewOrderWizard from './pages/NewOrderWizard';
import StepCostura from './wizardSteps/StepCostura';
import OrderConfirmationClient from './pages/OrderConfirmationClient';
import OrderList from './pages/OrderList';
import UsersPage from './pages/UsersPage';
import SettingsPage from './pages/SettingsPage';
import CatalogoItensPage from './pages/CatalogoItensPage';

// Esqueleto para expansão futura (Dashboard, Pedido, etc)
function App() {
  return (
    <ThemeProvider theme={theme}>
      <GlobalStyle />
      <Router>
        <Routes>
          <Route path="/login" element={<LoginPage />} />
          <Route path="/dashboard" element={
            <ProtectedRoute>
              <AdminDashboard />
            </ProtectedRoute>
          } />
          <Route path="/pedidos" element={
            <ProtectedRoute>
              <OrderList />
            </ProtectedRoute>
          } />
          <Route path="/usuarios" element={
            <ProtectedRoute>
              <UsersPage />
            </ProtectedRoute>
          } />
          <Route path="/configuracoes" element={
            <ProtectedRoute>
              <SettingsPage />
            </ProtectedRoute>
          } />
          <Route path="/novo-pedido" element={
            <ProtectedRoute>
              <NewOrderWizard />
            </ProtectedRoute>
          } />
          <Route path="/pedido/:id" element={<OrderConfirmationClient />} />
          <Route path="/catalogo-itens" element={
            <ProtectedRoute>
              <CatalogoItensPage />
            </ProtectedRoute>
          } />
          <Route path="*" element={<Navigate to="/login" replace />} />
        </Routes>
      </Router>
    </ThemeProvider>
  );
}

export default App;
